//import Connection from '../../controller/Connection'
import Devices from '../../controller/Devices'

export default 'hue-extended'
export const namespace = 'hue-extended'

export const deviceObjectType = 'channel';
export const devicePattern = '(lights|groups|sensors)+\\.(\\d{3}\\-[^.]+)$|([^.]+\\-\\d{3})$';


/**
 *
 *
 * @param	{Object}	deviceStructure
 * @param	{String}	deviceStructure.root		root state
 * @param	{Array}		deviceStructure.states		all states
 * @param	{Object}	deviceStructure.values		all states with its value
 * @return	{Object}	device
 */
export function parse(deviceStructure, options) {
	
	return new Promise(resolve => {
		
		let device = {}
		device.name = deviceStructure.values[deviceStructure.root + '.name'].val;
		device.states = {}
		
		// group or light
		if (deviceStructure.root.indexOf('.groups.') > -1 || deviceStructure.root.indexOf('.lights.') > -1) {
			
			device = {
				...device,
				'function': 'light',
				'states': {
					'power': {
						'state': '.action.on',
						'action': '.action.on'
					},
					'reachability': '.state.reachable'
				}
			}
			
			// check if light has dimmer (level), white (colorTemperature) or color (hue) capabilities
			device.states = {
				'level': {
					'state': '.action.level',
					'action': '.action.level'
				},
				'colorTemperature': {
					'state': '.action.colorTemperature',
					'action': '.action.colorTemperature'
				},
				'hue': {
					'state': '.action.hue',
					'action': '.action.hue'
				},
			}
			
			// validate states
			device.states = Devices.validateStates(device.states, deviceStructure);
			
			// resolve
			resolve(device);
		}
		
		// sensor
		else if (deviceStructure.root.indexOf('.sensors.') > -1) {
			
			// check which states are available and thus supported
			device.states = {
				'power': {
					'state': '.action.on',
					'action': '.action.on'
				},
				'battery': '.config.battery',
				'reachability': '.config.reachable',
				'humidity': '.state.humidity',
				'temperature': '.state.temperature',
				'door': '.state.open'
			}
			
			// validate states
			device.states = Devices.validateStates(device.states, deviceStructure);
			
			// resolve
			resolve({
				...device,
				'function': 'other'
			});
		}
		
		// not supported
		else {
			resolve({});
		}
	});
}