import Connection from '../../controller/Connection'
import Devices from '../../controller/Devices'

export default 'HomeMatic IP'
export const namespace = 'hm-rpc'

export const deviceObjectType = 'device';



/**
 *
 *
 * @param	{Object}	deviceStructure
 * @param	{String}	deviceStructure.root		root state
 * @param	{Array}		deviceStructure.states		all states
 * @param	{Object}	deviceStructure.values		all states with its value
 * @return	{Object}	device
 */
export function parse(deviceStructure, options) {
	const socket = Connection.getConnection;
	
	return new Promise(resolve => {
		
		socket.getObject(deviceStructure.root).then(obj => {
			
			let device = {
				'name': obj.common.name,
				'function': 'other',
				'states': {
					'unreach': '.0.UNREACH',
					'lowBattery': '.0.LOWBAT',
				}
			}
			
			switch(obj.native.TYPE.toLowerCase()) {
				
				// HomeMatic IP - HEATING
				case 'hmip-bwth': // HmIP-BWTH
					device.function = 'heating';
					device.states = {
						'temperature': {
							'state': '.1.ACTUAL_TEMPERATURE'
						},
						'setTemperature': {
							'state': '.1.SET_POINT_TEMPERATURE',
							'action': '.1.SET_POINT_TEMPERATURE'
						},
						'humidity': {
							'state': '.1.HUMIDITY'
						},
						'boost': {
							'state': '.1.BOOST_MODE',
							'action': '.1.BOOST_MODE'
						},
						...device.states
					}
					break;
				
				// HomeMatic IP - WEATHER STATION
				case 'hmip-swo-b': 
					break;
				
				// HomeMatic IP - BLIND or JALOUSIE
				case 'hmip-broll': // HmIP-BROLL
					device.function = 'blind';
					device.states = {
						'level': {
							'state': '.3.LEVEL',
							'action': '.4.LEVEL'
						},
						'level2': {
							'state': '.3.LEVEL2',
							'action': '.4.LEVEL2'
						},
						'activity': {
							'state': '.3.ACTIVITY_STATE',
							'action': '.4.STOP'
						},
						...device.states
					}
					break;
				
				// HomeMatic IP - SWITCH
				case 'hmip-brc2': // HmIP-BRC2
					device.function = 'light';
					device.states = {
						'power': {
							'state': '.3.STATE',
							'action': '.4.STATE'
						},
						...device.states
					}
					break;
				
				
				// HomeMatic IP - DIMMER
				case 'hmip-bdt': // HmIP-BDT
					device.function = 'light';
					device.states = {
						'level': {
							'state': '.3.LEVEL',
							'action': '.4.LEVEL'
						},
						...device.states
					}
					break;
				
				
				// HomeMatic - WINDOW SENSOR
				case 'hm-sec-sco': // HM-Sec-SCo
				case 'hm-sec-sc-2': // HM-Sec-SC-2
					device.function = 'window';
					device.states = {
						'open': {
							'state': '.1.STATE',
						},
						...device.states
					}
					break;
				
				
				// HomeMatic - HEATING
				case 'hm-cc-rt-dn': // HM-CC-RT-DN
					device.function = 'heating';
					device.states = {
						'temperature': {
							'state': '.4.ACTUAL_TEMPERATURE'
						},
						'setTemperature': {
							'state': '.4.SET_TEMPERATURE',
							'action': '.4.SET_TEMPERATURE'
						},
						'boost': {
							'state': '.4.BOOST_MODE',
							'action': '.4.BOOST_MODE'
						},
						...device.states
					}
					break;
				
				
				// HomeMatic - SWITCH
				case 'hm-lc-sw1pbu-fm': // HM-LC-Sw1PBU-FM
					device.function = 'light';
					device.states = {
						'power': {
							'state': '.1.STATE',
							'action': '.1.STATE'
						},
						...device.states
					}
					break;
				
				
				// HomeMatic - BLIND
				case 'hm-lc-bl1pbu-fm': // HM-LC-Bl1PBU-FM
				case 'hm-lc-bl1-fm': // HM-LC-Bl1-FM
					device.function = 'blind';
					device.states = {
						'level': {
							'state': '.1.LEVEL',
							'action': '.1.LEVEL'
						},
						'activity': {
							'state': '.1.WORKING',
							'action': '.1.STOP'
						},
						...device.states
					}
					break;
				
				
				default:
					break;
			}
			
			// validate states
			device.states = Devices.validateStates(device.states, deviceStructure);
			
			// resolve
			resolve(device);
		});
	});
}